*{
    margin: 0;
    padding: 0;
}

    

.container{
  text-align: center;
  margin-top: 50px;
 
  
 
  
}
.container h1{
  font-size: 40px;
  margin-bottom: 20px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: darkblue;
  
}
.container form{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: white;
   
   

}
.container input{
    font-size: 15px;
    outline: none;
    padding: 15px 10px;
    width: 20%;
    border-radius: 5px;
    /* margin: 10px 0; */
    margin-bottom: 17px;
    border: 1px solid darkblue;
   
   
}

/* input::placeholder{
    color: white;
    font-size: 18px;
} */
.container label{
    font-size: 20px;
    text-align: center;
}
.button{
    border-radius: 5px;
    width: 100px;
    padding: 10px;
    background-color: darkblue;
    color:white;
    border: 1px solid black;
    outline: none;
    cursor: pointer;
}
.sel{
    width: 22%;
    padding: 15px 10px;
    border-radius: 5px;
    border:1px solid darkblue;
    margin-bottom: 15px;
    outline: none;
}

